import React from 'react';
import './LinkBack.scss';
import { Link } from 'react-router-dom';
import navLeft from '../../img/UI/navLeft.svg';

const LinkBack = ({path, title, onClick}) => {
  return (
    <div className='header'>
      {onClick 
        ?
          <button to={path} className='header__backIcon' onClick={onClick}>
            <img src={navLeft} alt="left" />
          </button>
        :
          <Link to={path} className='header__backIcon'>
            <img src={navLeft} alt="left" />
          </Link>
      } 
      <p className='header__title'>{title}</p>
    </div>
  );
};

export default LinkBack;