import React, { useEffect, useState } from 'react';
import './Archive.scss';
import Promo from './../../components/Promo/Promo';
import SearchInput from './../../components/SearchInput/SearchInput';
import filterByCategory  from '../../img/filter/category.svg';
import filterByDate  from '../../img/filter/date.svg';
import list  from '../../img/filter/list.svg';
import chart  from '../../img/filter/chart.svg';
import SortByDateModal from '../../components/FilterModals/SortByDate/SortByDateModal';
import { observer } from 'mobx-react-lite';
import LinkBack from './../../components/LinkBack/LinkBack';
import { ARCHIVE_CHART_ROUTE, ARCHIVE_ROUTE, ARCHIVE_TRANSACTIONS_ROUTE, OWERVIEW_ROUTE, UI_MOBILE } from './../../utils/consts';
import SortByCategory from './../../components/FilterModals/SortByCategory/SortByCategory';
import FilterByStatus from './../../components/FilterModals/FilterByStatus/FilterByStatus';
import { useDebounce } from './../../hooks/useDebounce';
import FilterStore from '../../store/FilterStore';
import { Outlet, useNavigate, useLocation } from 'react-router-dom';
import transactionsSvg from '../../img/filter/transactions.svg';
import { useSorting } from './../../hooks/useSorting';

const Archive = observer(() => {  
  const [showCategory, setShowCategory] = useState(false);
  const [showDate, setShowDate] = useState(false);
  const [showFilter, setShowFilter] = useState(false);
  const [isChartPage, setIsChartPage] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if(location.pathname === ARCHIVE_ROUTE + '/' + ARCHIVE_CHART_ROUTE) setIsChartPage(true);
  }, [])

  const filterHandler = (e) => {
    setShowCategory(false);
    setShowDate(false);
    setShowFilter(false);
    if(e.currentTarget.id === 'category') showCategory ? setShowCategory(false) : setShowCategory(true);
    if(e.currentTarget.id === 'date') showDate ? setShowDate(false) : setShowDate(true);
    if(e.currentTarget.id === 'filter') showFilter ? setShowFilter(false) : setShowFilter(true);
  }

  const sortSearch = useSorting();
  const sortSearchDebounce = useDebounce(sortSearch, 500);

  return (
    <>
      <LinkBack title='Archive transactions' path={OWERVIEW_ROUTE}/>
      <div className='archive'>
        <div className='archive__page border-gradient'>
          <div className='archive__header'>
            <h1 className='archive__title'>Archive transactions</h1>
            <SearchInput classesContainer='archive__search' 
              value={FilterStore.searchValue}
              onChange={(value) => {
                FilterStore.setFilteredBySearch(true);
                FilterStore.setSearchValue(value);
                sortSearchDebounce();
                if(location.pathname === ARCHIVE_ROUTE + '/' + ARCHIVE_CHART_ROUTE) {
                  setIsChartPage(false);
                  navigate(ARCHIVE_TRANSACTIONS_ROUTE);
                }
              }}/>
            <div className='archive__filtersTitle'>Filter by</div>
            <div className='archive__filters'>
              <div className='archive__filter'>
                <button id='filter' className='filter__icon' onClick={filterHandler}>
                  <img src={list} alt="Filter List" />
                </button>
                <FilterByStatus isShow={showFilter} setShow={setShowFilter} />
              </div>
              <div className='archive__filter'>
                <button id='category' className='filter__icon' onClick={filterHandler}>
                  <img src={filterByCategory} alt="Filter By Category" />
                </button>
                <SortByCategory isShow={showCategory} setShow={setShowCategory} />
              </div>
              <div className='archive__filter'>
                <button id='date' className='filter__icon' onClick={filterHandler}>
                  <img src={filterByDate} alt="Filter By Date" />
                </button>
                <SortByDateModal  isShow={showDate} setShow={setShowDate} />
              </div>
              <div className='archive__filter' onClick={filterHandler}>
                {isChartPage 
                  ?
                    <button className='filter__icon' onClick={() => {
                      setIsChartPage(false);
                      navigate(ARCHIVE_TRANSACTIONS_ROUTE)
                    }}>
                      <img src={transactionsSvg} alt="Filter Trans" />
                    </button>
                  :   
                    <button className='filter__icon' onClick={() => {
                      setIsChartPage(true);
                      navigate(ARCHIVE_CHART_ROUTE)
                    }}>
                      <img src={chart} alt="Filter Chart" />
                    </button>       
                }
              </div>
            </div>
          </div>
          <Outlet />
        </div>
        <Promo title='Read the latest news on the system update'/>
      </div>
    </>
  );
});

export default Archive;