import React from 'react';
import './FilterByStatus.scss';
import close from '../../../img/UI/close.svg';
import InputField from './../../InputField/InputField';
import { validateMoney } from './../../../utils/validate';
import FilterStore from '../../../store/FilterStore';
import { observer } from 'mobx-react-lite';
import { useSorting } from './../../../hooks/useSorting';

const FilterByStatus = observer(({isShow, setShow, onClick}) => {
 
  const sortByRangeMoney = useSorting(setShow);
         
  return (
    <>
      { isShow &&
        <div className='filterByStatus' onClick={onClick}>
          <div className='filterByStatus__header'>
            <h1 className='filterByStatus__title'>Filter</h1>
            <img src={close} alt="close" className='filterByStatus__close' onClick={() => setShow(false)}/>
          </div>
          <div className='filterByStatus__choice'>
            <div className=''>Status</div>
            <div className='filterByStatus__points'>
              <button className='filterByStatus__point'>Conducted</button>
              <button className='filterByStatus__point'>Blocked</button>
            </div>
          </div>
          <div className='filterByStatus__choice'>
            <div className=''>Currency</div>
            <div className='filterByStatus__points'>
              <button className='filterByStatus__point'>USD</button>
              <button className='filterByStatus__point'>EUR</button>
              <button className='filterByStatus__point'>FR</button>
              <button className='filterByStatus__point'>PD</button>
            </div>
          </div>
          <div className='filterByStatus__choice'>
            <div className=''>Amount</div>
            <div className='filterByStatus__points filterByStatus__points-nomargin'>
              <InputField 
                inputType='number' 
                titleName='From' 
                placeholder='200' 
                value={FilterStore.from}
                onChange={(e) => {
                  validateMoney(e.target, 0.00);
                  FilterStore.setFrom(e.target.value);
                }}/>
              <InputField 
                inputType='number' 
                titleName='To' 
                placeholder='400'
                value={FilterStore.to}
                onChange={(e) => {
                  validateMoney(e.target, 0.00);
                  FilterStore.setTo(e.target.value);
                }}/>
            </div>
          </div>
          <div className='filterByStatus__buttons'>
            <button className='btn filterByStatus__apply' 
              disabled={FilterStore.from === '' || FilterStore.to === '' || +FilterStore.from > +FilterStore.to ? true : false}
              onClick={() => {
                FilterStore.setFilterByAmount(true);
                sortByRangeMoney();
              }}
            >
                Apply
            </button>
            <button className='btn filterByStatus__clear' onClick={() => {
              FilterStore.setFrom('');
              FilterStore.setTo('');
              FilterStore.setFilterByAmount(false);
              sortByRangeMoney();
            }}>Clear all filters</button>
          </div>
        </div>
      }
    </>
  )
});

export default FilterByStatus;